/* @font-face {
  font-family: "Fira Code";
  src: url("https://cdn.rawgit.com/tonsky/FiraCode/master/distr/ttf/FiraCode-Regular.ttf");
} */

code[class*="language-"],
pre[class*="language-"] {
  color: #ffffff;
  background: 0 0;
  font-family: "Hack", Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace !important;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}
pre[class*="language-"] {
  /* padding: 1em 2em; */
  margin: 2.5em 0;
  overflow-y: hidden;
}
:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #282c34;
}
:not(pre) > code[class*="language-"] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal;
}
.token.block-comment,
.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
  color: #b2b2b2;
}
.token.punctuation {
  color: #88c6be;
}
.token.deleted,
.token.namespace,
.token.tag {
  color: #fc929e;
}
.token.attr-name {
  color: #c5a5c5;
}
.token.function-name {
  color: #6196cc;
}
.token.boolean,
.token.function,
.token.number {
  color: #79b6f2;
}
.token.class-name,
.token.constant,
.token.property,
.token.symbol {
  color: #fac863;
}
.token.atrule,
.token.builtin,
.token.important,
.token.keyword,
.token.selector {
  color: #c5a5c5;
}
.token.attr-value,
.token.char,
.token.regex,
.token.variable {
  color: #7ec699;
}
.token.string {
  color: #8dc891;
}
.token.entity,
.token.operator,
.token.url {
  color: #67cdcc;
}
.token.bold,
.token.important {
  font-weight: 700;
}
.token.italic {
  font-style: italic;
}
.token.entity {
  cursor: help;
}
.token.inserted {
  color: green;
}
