.accordion {
  padding-left: 40px;
}

.accordion__item--has-icon {
  position: relative;
}

/* DO NOT TOUCH */
.accordion__item {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.accordion__arrow {
  display: inline-block;
  width: 24px;
  height: 12px;
  position: absolute;
  top: 40px;
  right: 15px;
  margin-top: -6px;
}

/* #endregion */

.accordion__title {
  cursor: pointer;
  margin: 5px 0;
}

.accordion__title:hover .acc-item-title,
.accordion__title:focus .acc-item-title {
  color: rgb(26, 26, 26) !important;
}

.accordion__title:hover .accordion__arrow::after,
.accordion__title:hover .accordion__arrow::before,
.accordion__title:focus .accordion__arrow::after,
.accordion__title:focus .accordion__arrow::before {
  background-color: rgb(26, 26, 26) !important;
}

.accordion__body {
  padding: 5px 10px 20px;
  display: block;
}

.accordion__body a.active {
  font-weight: bold;
  position: relative;
}

.accordion__body a.active::before {
  content: "";
  position: absolute;
  left: -21px;
  top: 1px;
  width: 5px;
  background-color: #61dafb;
  height: 19px;
}

.accordion__body--hidden {
  display: none;
  opacity: 0;
}

.accordion__title > *:last-child,
.accordion__body > *:last-child {
  margin-bottom: 0;
}

.accordion__arrow {
  display: inline-block;
  width: 24px;
  height: 12px;
  position: relative;
  top: 14%;
  right: 0;
  left: 10px;
}

.accordion__arrow::after,
.accordion__arrow::before {
  display: block;
  position: absolute;
  top: 50%;
  width: 8px;
  height: 2px;
  background-color: rgb(109, 109, 109);
  content: "";
}

.accordion__arrow::before {
  left: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

[aria-expanded="true"] .accordion__arrow::before,
[aria-selected="true"] .accordion__arrow::before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.accordion__arrow::after {
  right: 7px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

[aria-expanded="true"] .accordion__arrow::after,
[aria-selected="true"] .accordion__arrow::after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.accordion__arrow::before,
.accordion__arrow::after {
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
}

