}

.open .line-menu.start {
  -webkit-transform: rotate(-90deg) translateX(3px);
          transform: rotate(-90deg) translateX(3px);
}

.line-menu.end {
  align-self: flex-end;
  -webkit-transition: -webkit-transform 200ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: -webkit-transform 200ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 200ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 200ms cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 200ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  -webkit-transform-origin: left;
