    height: 40px;
  }

  #navbar #nav-link-version,
  #navbar #nav-link-git {
    display: none !important;
  }

  #navbar ul {
    line-height: 1.6; /* Fit scrollbar to navbar bottom, yo! */
  }

  #navbar #navbar-brand {
    margin-left: 10px !important;
  }

  .navbar > .container,
  .navbar > .container-fluid {
    flex-wrap: unset;
  }

  .navbar-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .collapse.show.navbar-collapse {
    display: flex;
    overflow-x: auto;
  }
  #navbar-brand-title {
    display: none;
  }
  /* Avoid similar class names and ids */
  .navbar-brand {
    margin-right: 0;
  }
  #navbar-brand {
    margin-left: 0 !important;
    padding: 0 0 0 15px !important;
  }
  #navbar-links-left a {
    padding: 6px 10px;
    font-size: 15px;
  }
  #sidebar {
    position: absolute;
    top: 40px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: auto;
    z-index: -1;
    padding-top: 0 !important;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  #sidebar.active-mobile {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    z-index: 1;
    opacity: 1;
    padding-left: 15px !important;
    padding-top: 10px !important;
  }
  #sidebar-fab {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 44px;
    right: 40px;
    width: 60px;
    height: 60px;
    background-color: #20232a;
    color: #61dafb;
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    z-index: 2;
  }
  #main-content-wrapper {
    padding: 0;
  }
  #main-content {
    padding: 20px 0 0 0 !important;
  }
  #main-content > * {
    margin-left: 20px;
    margin-right: 20px;
  }
  #main-content > blockquote,
  #main-content > pre {
    margin-left: 0;
    margin-right: 0;
  }
  #main-container {
    overflow-x: hidden;
    padding: 0;
  }
  #main-content > h1:first-of-type {
    font-size: 40px;
    line-height: 45px;
  }
  #main-content > p:first-of-type {
    font-size: 18px;
  }
  #pagination .page-previous a,
  #pagination .page-next a {
    font-size: 16px;
  }
  #footer {
    padding-top: 0;
    padding-bottom: 50px;
  }
  #footer .oss-logo img {
    margin: 2em auto;
  }
  #page {
    margin-top: 40px !important;
  }
  /* !! Search */

  /* input#navbar-search {
    
  } */

  #search-container {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  #navbar-search-icon {
    display: inline-block;
    position: absolute;
    top: 13px;
    right: 16px;
    z-index: 1;
  }

  #search-results {
    display: none;
    top: 40px !important;
    left: -4px !important;
  }

  pre[class*="language-"] {
    padding: 1em !important;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-left: 25px !important;
  }
}

